"use client"

import type React from "react"

import { useApp } from "@/lib/app-context"
import { useState } from "react"
import { MessageSquare, Trash2, Plus } from "lucide-react"

export function AnnouncementBoard() {
  const { announcements, addAnnouncement } = useApp()
  const [showForm, setShowForm] = useState(false)
  const [formData, setFormData] = useState({
    title: "",
    message: "",
    priority: "normal" as const,
  })

  const handleAddAnnouncement = (e: React.FormEvent) => {
    e.preventDefault()
    const newAnnouncement = {
      title: formData.title,
      content: formData.message, // Map 'message' to 'content' for backend
      priority: formData.priority,
      author: "Current User" // Backend handles date/id usually, but DTO might need author
    }
    addAnnouncement(newAnnouncement)
    setFormData({ title: "", message: "", priority: "normal" })
    setShowForm(false)
  }

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case "critical":
        return "border-l-4 border-red-500 bg-red-50/50"
      case "high":
        return "border-l-4 border-amber-500 bg-amber-50/50"
      case "normal":
        return "border-l-4 border-slate-400 bg-slate-50/50"
      default:
        return ""
    }
  }

  const getPriorityBadge = (priority: string) => {
    switch (priority) {
      case "critical":
        return "bg-red-100 text-red-800"
      case "high":
        return "bg-amber-100 text-amber-800"
      default:
        return "bg-slate-100 text-slate-800"
    }
  }

  return (
    <div className="min-h-screen bg-background p-8">
      <div className="mb-8">
        <div className="flex justify-between items-start mb-6">
          <div>
            <h1 className="text-3xl font-bold text-foreground">Announcement Board</h1>
            <p className="text-muted-foreground mt-2">Real-time communication with team and job sites</p>
          </div>
          <button
            onClick={() => setShowForm(!showForm)}
            className="px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors font-medium flex items-center gap-2"
          >
            <Plus size={20} /> New Announcement
          </button>
        </div>

        {showForm && (
          <form onSubmit={handleAddAnnouncement} className="bg-card p-6 rounded-lg border border-border mb-6 space-y-4">
            <input
              type="text"
              placeholder="Announcement title"
              value={formData.title}
              onChange={(e) => setFormData({ ...formData, title: e.target.value })}
              required
              className="w-full px-4 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-primary focus:border-transparent"
            />
            <textarea
              placeholder="Message content"
              value={formData.message}
              onChange={(e) => setFormData({ ...formData, message: e.target.value })}
              required
              rows={3}
              className="w-full px-4 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-primary focus:border-transparent"
            />
            <select
              value={formData.priority}
              onChange={(e) => setFormData({ ...formData, priority: e.target.value as any })}
              className="w-full px-4 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-primary focus:border-transparent"
            >
              <option value="normal">Normal</option>
              <option value="high">High</option>
              <option value="critical">Critical</option>
            </select>
            <div className="flex gap-2">
              <button
                type="submit"
                className="flex-1 px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 font-medium"
              >
                Post
              </button>
              <button
                type="button"
                onClick={() => setShowForm(false)}
                className="flex-1 px-4 py-2 bg-slate-100 text-foreground rounded-lg hover:bg-slate-200 font-medium"
              >
                Cancel
              </button>
            </div>
          </form>
        )}
      </div>

      <div className="space-y-4">
        {announcements.map((announcement) => {
          const date = new Date(announcement.timestamp).toLocaleString()
          return (
            <div
              key={announcement.id}
              className={`bg-card p-6 rounded-lg border border-border transition-all hover:shadow-lg ${getPriorityColor(announcement.priority)}`}
            >
              <div className="flex justify-between items-start mb-3">
                <div className="flex-1">
                  <div className="flex items-center gap-2 mb-2">
                    <span
                      className={`px-2 py-1 rounded text-xs font-medium ${getPriorityBadge(announcement.priority)}`}
                    >
                      {announcement.priority.toUpperCase()}
                    </span>
                  </div>
                  <h3 className="text-lg font-bold text-foreground">{announcement.title}</h3>
                  <p className="text-xs text-muted-foreground mt-1">
                    {announcement.author} • {date}
                  </p>
                </div>
                <button className="p-2 hover:bg-destructive/10 rounded transition-colors">
                  <Trash2 size={18} className="text-destructive" />
                </button>
              </div>

              <p className="text-foreground mb-4 leading-relaxed">{announcement.message}</p>

              <div className="flex items-center gap-2 pt-4 border-t border-border">
                <MessageSquare size={16} className="text-muted-foreground" />
                <span className="text-sm text-muted-foreground">Discussion thread</span>
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}
